.mainContainer {
  display: flex;
  align-items: center;
  justify-content: center;

  height: 90vh;

  background-color: #f9f9fa;
}

.formContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

  padding: 20px 33px;

  background-color: #ffffff;
  border-radius: 2px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1), -1px -1px 2px rgba(0, 0, 0, 0.1);
}

.span {
  margin-top: 25px;

  font-size: 14px;
  font-weight: 600;
  color: #000000;
  text-align: center;

  opacity: 0.5;
}

.formContainer h1 {
  margin: 30px 0 0;

  font-size: 26px;
  text-align: center;
}

.signInBtn {
  margin-top: 20px;
  margin-bottom: 20px;

  font-weight: 600;
  color: #366eff;
  text-decoration-line: underline;
}

.emailInput {
  border: none;
  border-bottom: 1.6px solid black;
}

.error {
  font-weight: 400;
  color: red;
}

.forgotPassword {
  margin-top: 20px;
  margin-bottom: 20px;

  color: black;
  text-align: right;
  text-decoration: none;
}

.formControlLabel {
  font-family: 'Montserrat';
  font-size: 16px;
  font-weight: 500;
  font-style: normal;
  color: black;
}
