.mainContainer {
  display: flex;
  align-items: center;
  justify-content: center;

  height: 90vh;

  background-color: #f9f9fa;
}

.formContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

  width: 350px;
  padding: 20px 33px;

  background-color: #ffffff;
  border-radius: 2px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1), -1px -1px 2px rgba(0, 0, 0, 0.1);
}

.formContainer span {
  margin-top: 25px;

  font-size: 14px;
  font-weight: 600;
  line-height: 24px;
  color: #000000;
  text-align: center;

  opacity: 0.5;
}

.formContainer h1 {
  margin-top: 30px;

  font-size: 26px;
}

.instruction {
  margin-bottom: 30px;
}
